.display {
  font-family: Led, 'Comic Sans MS', cursive;
  white-space: pre-wrap; }

table.mk52 {
  background: #3F3F49;
  border-collapse: collapse;
  border-spacing: 0;
  border-radius: 7px; }
  table.mk52 td {
    padding: 0; }
  table.mk52 .display {
    font-family: Led, 'Comic Sans MS', cursive;
    font-size: 19pt;
    color: yellow;
    white-space: pre-wrap;
    opacity: 1 !important;
    filter: "alpha(opacity=100)" !important; }
  table.mk52 .display.wait {
    opacity: 0.2 !important;
    filter: "alpha(opacity=20)" !important; }
  table.mk52 .keyboard {
    font-size: 12pt;
    background: #2F2B39;
    border: none; }
    table.mk52 .keyboard tr {
      height: 50px;
      border: none; }
    table.mk52 .keyboard tr td {
      font-size: 0.8em;
      border: none;
      margin: 0px;
      padding: 0px 5px;
      width: 58px;
      max-width: 58px;
      height: 50px;
      max-height: 50px;
      text-align: center;
      vertical-align: center;
      color: #EBD108; }
    table.mk52 .keyboard tr td i {
      color: #4BA9F3;
      font-style: normal; }
    table.mk52 .keyboard tr td b {
      color: #ECF0E4;
      font-weight: normal;
      font-style: italic; }
    table.mk52 .keyboard tr td span,
    table.mk52 .keyboard tr td span:active,
    table.mk52 .keyboard tr td span:focus,
    table.mk52 .keyboard tr td span:hover {
      font-size: 1.2em;
      display: block;
      text-align: center;
      cursor: pointer;
      border-radius: 4px;
      border: 1px solid #adadad;
      color: #ECF0E4;
      font-weight: bold;
      vertical-align: middle;
      -webkit-box-shadow: 3px 3px 3px black;
      box-shadow: 3px 3px 3px black;
      -webkit-transition: 0.3s;
      -o-transition: 0.3s;
      transition: 0.3s; }
    table.mk52 .keyboard tr td span:focus,
    table.mk52 .keyboard tr td span:hover {
      -webkit-box-shadow: 1px 1px 3px black;
      box-shadow: 1px 1px 3px black; }
    table.mk52 .keyboard tr td span.w {
      color: #2F2B39;
      background-color: #ECF0E4; }
    table.mk52 .keyboard tr td span.f {
      color: #2F2B39;
      background-color: yellow; }
    table.mk52 .keyboard tr td span.k {
      color: #ECF0E4;
      background-color: #4BA9F3; }
    table.mk52 .keyboard tr td span.r {
      color: #ECF0E4;
      background-color: red; }
  table.mk52 .mk_a0 {
    background: url(calc/a0.png) no-repeat;
    height: 66px; }
  table.mk52 .mk_a3 {
    background: url(calc/a3a.png) no-repeat 50% 0px, url(calc/a3.png) repeat-x;
    height: 66px; }
  table.mk52 .mk_a4 {
    background: url(calc/a4.png) no-repeat;
    width: 33px;
    height: 66px; }
  table.mk52 .mk_b0 {
    background: url(calc/b0.png) repeat-y;
    width: 40px; }
  table.mk52 .mk_b1 {
    background: url(calc/b1.png) no-repeat;
    width: 206px;
    height: 66px;
    text-align: center; }
  table.mk52 .mk_b2 {
    background: url(calc/b2.png) repeat-y;
    width: 55px; }
  table.mk52 .mk_b4 {
    background: url(calc/b4.png) repeat-y;
    width: 33px; }
  table.mk52 .mk_c0 {
    background: url(calc/c0.png) no-repeat;
    width: 40px;
    height: 41px; }
  table.mk52 .mk_c1 {
    background: url(calc/c1.png) repeat-x;
    width: 206px;
    height: 41px; }
  table.mk52 .mk_c2 {
    background: url(calc/c2.png) no-repeat;
    width: 55px;
    height: 41px; }
  table.mk52 .mk_d0 {
    background: url(calc/d0.png) repeat-y;
    width: 40px;
    height: 98px; }
  table.mk52 .mk_d1 {
    background: url(calc/d1.png) no-repeat, url(calc/d1b.png);
    width: 206px; }
  table.mk52 .mk_d2 {
    background: url(calc/d2.png) repeat-y;
    width: 55px; }
  table.mk52 .mk_e0 {
    background: url(calc/e0.png) no-repeat;
    width: 40px;
    height: 17px; }
  table.mk52 .mk_e1 {
    background: url(calc/e1.png) repeat-x;
    height: 17px; }
  table.mk52 .mk_e2 {
    background: url(calc/e2.png) no-repeat;
    width: 55px;
    height: 17px; }
  table.mk52 .mk_e3 {
    background: url(calc/e3.png) repeat-x;
    height: 17px; }
  table.mk52 .mk_e4 {
    background: url(calc/e4.png) no-repeat;
    width: 33px;
    height: 17px; }



